<template>
  <div class="hello">
    <c-title :text="title" ></c-title>
    <img class="logo" src="../assets/images/logo.png">
    <h1 class="welcome"> vue</h1>
    <router-link :to="{ name: 'home'}">to home</router-link>
    <router-link :to="{ name: 'list'}">to list</router-link>
    <!-- <div v-html="content"></div> -->
    <swipe class="swiper_home" :speed="600" >
      <swipe-item class="slide1" v-for="banner in banners"> <img :src="banner" alt=""></swipe-item>
    </swipe>
    <input type="text" v-model="username" name="" id="" placeholder=""> 
    <button type="button" @click="send()"></button>
    <nav-bar></nav-bar> 
  </div>
</template>

<script>

  import {mapState} from 'vuex';
  import cTitle from 'components/title';
  import Toast from 'vue-toast-mobile';
  import { Swipe, SwipeItem } from 'vue-swipe';
  import navBar from '../components/navBar.vue';

  export default {
    data () {
      return {
        title: 'Hello Vue!!!',
        username: '',
        content: '',
        banners: ''
      }
    },
    methods: {
      async getContent () {
        const response = await fetch(this.$api.home);
        this.content = await response.json();

        this.$store.commit('UPDATE_LOADING', false)
        this.banners = this.content.datas.topimage_ary;

      },
      send() {
        let data = new FormData()
            data.append('username', this.username)

        fetch(this.$api.getSmsCode, {
          method: 'POST',
          body: data
        })
        .then(function(response) {
          return response.json().then(function(json) {
            
            console.log(json);
            if(json.code == '200'){
              Toast(json.datas.msg);
            }
          });
        })
        .then(function(res) {
          console.log(res)
        })
        .catch(function(ex) {
          alert(',!')
        })
      }
    },
    mounted () {
      this.getContent();
      
    },

    components: {
      Swipe,
      SwipeItem,
      cTitle,
      navBar
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus" scoped>
@import "../../node_modules/vue-swipe/dist/vue-swipe.css";
color = #ff6600;
  h1
    color: color

  .logo {
    width: 100px;
    height: 100px;
    a {
      color: #42b983;
      text-decoration: none;
    }
  }
  

.swiper_home {
  height: 200px;
  position: relative;
  overflow: hidden;
}
</style>



